import React,{useState,useEffect} from 'react'
const axios=require('axios')

 
function Check(){
    const [arr,setArr]=useState([])
    useEffect(()=>{
        axios.get("http://localhost:5000/api")
             .then((res=>{
                     return res.data
             })).then((jsonRes)=>{
                 setArr(jsonRes.users)
             })
    },[])
    return(
        <>
            <h1>yeah</h1>
            {arr.map(i=> <li>{i}</li>)}
        </>
    )
  }

export default Check